import React, {useEffect, useState} from 'react'
import './styles.css'
import NewsCard from "../NewsCard";
import {Link} from "react-router-dom";

export default function HomePage(props) {
    const [allNews, setAllNews] = useState([]);
    const [tabsList, setTabsList] = useState([])
    let tempNewsList = [];
    let artCat = Object.freeze({
        popular: "popular",
        notPopular: "notPopular",
        hasImage: "hasImage",
        hasNoImage: "hasNoImage",
        hasSubs: "hasSubs",
        hasNoSubs: "hasNoSubs",
        isComplete: "isComplete",
        justTitle: "justTitle"
    });

    async function fetchNews() {
        setAllNews([]);
        let url = props.newsUrl;
        console.log("url is :" + url)
        let response = await fetch(url);
        let jsonMap = await response.json();
        tempNewsList = jsonMap['top'].map((story) => addCategoriesToStory(story));
        console.debug("tempNews list updated, its length is ", tempNewsList.length);
        // /TODO: sort stories in different categories here
        let divsList = createSectionsDivList(tempNewsList);
        // divsList.push(getSection1(tempStoriesList))
        // divsList.push(getSection2(tempStoriesList))
        // divsList.push(getSection3(tempStoriesList))
        setAllNews(divsList);

    }

    function createSectionsDivList() {
        console.log("creating Sections of newsStories")
        let divsList = [];
        //step 1 create first section, get main story
        while (tempNewsList.length > 0) {
            divsList.push(getSection1())
            divsList.push(getSection2())
            divsList.push(getSection3())
            divsList.push(getSection2())
            if (divsList.slice(divsList.length - 4).every((section) => section === "")) {
                if (tempNewsList.length >= 4)
                    divsList.push(getSection4())
                else break;
                console.debug("remaining stories are ", tempNewsList.length);

            }
        }
        return divsList;
    }

    function getStoryAndUpdateList(catList, storiesList) {
        let index;
        for (let i = 0; i < storiesList.length; i++) {
            let selectedStory = storiesList[i];
            if (catList.every((ct) => selectedStory.categories.some((e) => e === ct))) {
                index = i;
                break;
            }
        }
        console.log("found main story, its index is :" + index);
        if (index === undefined) {
            return ""
        }
        let retStory = storiesList.splice(index, 1)[0];
        console.log(retStory)
        return retStory
    }

    function getMainStory(catList, storiesList) {

        let index;
        for (let i = 0; i < storiesList.length; i++) {
            let selectedStory = storiesList[i];
            if (catList.every((ct) => selectedStory.categories.some((e) => e === ct))) {
                index = i;
                break;
            }
        }
        console.log("found main story, its index is :" + index);
        let retStory = storiesList.splice(index, 1)[0];
        console.log(retStory)
        return retStory
    }

    function getStdColsList(storiesList, numOfStdColNeeded) {
        let storiesListCopy = storiesList.slice();
        let stdColList = [];
        let stdCol = [];
        while (stdColList.length < numOfStdColNeeded) {
            stdCol = [];
            while (stdCol !== null && stdCol.length === 0) {
                let catAList = [];
                let catBList = [];
                let catCList = [];
                let catDList = [];
                for (let i = 0; i < storiesListCopy.length; i++) {
                    let newsStory = storiesListCopy[i];
                    //fill catA list with story index
                    if (catAList.length > 0 && newsStory.categories.includes(artCat.hasNoImage)) {
                        console.log("pushing to catA1  list- has no image")
                        catAList.push(i);
                    } else if (catAList.length === 0 && newsStory.categories.includes(artCat.popular) && newsStory.categories.includes(artCat.hasImage)) {
                        catAList.push(i);
                        console.log("pushing to catA0 list - isPopular and has image")
                    }

                    //fill catB list with story index
                    if (newsStory.categories.includes(artCat.notPopular) && newsStory.categories.includes(artCat.isComplete)) {
                        catBList.push(i)
                        console.log("pushing to catB list")
                    }
                    //fill catC list with story index
                    if ((newsStory.categories.includes(artCat.hasNoImage) && newsStory.categories.includes(artCat.hasSubs))) {
                        catCList.push(i)
                        console.log("pushing to catC list")
                    }
                    //fill catD list with story index
                    if (newsStory.categories.includes(artCat.justTitle)) {
                        catDList.push(i)
                        console.log("pushing to catC list")
                    }

                    if (catAList.length > 1) {

                        console.log(catAList)
                        stdCol.push(storiesListCopy.splice(catAList[0], 1)[0]);
                        stdCol.push(storiesListCopy.splice(catAList[1] - 1, 1)[0]);
                        console.log("catA full" + '1st title :' + stdCol[0].title + " 2nd title : " + stdCol[1].title)
                        break;
                    } else if (catBList.length > 1) {
                        console.log(catBList)
                        stdCol.push(storiesListCopy.splice(catBList[0], 1)[0]);
                        stdCol.push(storiesListCopy.splice(catBList[1] - 1, 1)[0]);
                        console.log("catB full" + '1st title :' + stdCol[0].title + " 2nd title : " + stdCol[1].title)
                        break;
                    } else if (catCList.length > 2) {
                        stdCol.push(storiesListCopy.splice(catCList[0], 1)[0]);
                        stdCol.push(storiesListCopy.splice(catCList[1] - 1, 1)[0]);
                        stdCol.push(storiesListCopy.splice(catCList[2] - 2, 1)[0]);
                        console.log("catC full" + '1st title :' + stdCol[0].title + " 2nd title : " + stdCol[1].title + " 3rd title : " + stdCol[2].title);
                        break;
                    } else if (catDList.length > 3) {
                        stdCol.push(storiesListCopy.splice(catDList[0], 1)[0]);
                        stdCol.push(storiesListCopy.splice(catDList[1] - 1, 1)[0]);
                        stdCol.push(storiesListCopy.splice(catDList[2] - 2, 1)[0]);
                        stdCol.push(storiesListCopy.splice(catDList[3] - 3, 1)[0]);
                        console.log("catD full" + catAList.join(",") + '1st title :' + stdCol[0].title + " 2nd title : " + stdCol[1].title + " 3rd title : " + stdCol[2].title + " 4th title : " + stdCol[3].title);
                        break;
                    } else if (i === storiesListCopy.length - 1) {
                        //that means we are at the end of the list and can't segregate stories in desired standard sections anymore
                        //so just add remaining stories to the list and end the section
                        console.log("can't get std col anymore, discarding colsList and returning empty Std ColList instead")
                        stdCol = null;
                        break;
                    }
                }
            }

            stdColList.push(stdCol);
            if (stdColList.includes(null)) return [[], storiesList];
        }

        return [stdColList, storiesListCopy];
    }

    function getSection1() {
        let tempNewsListCopy = tempNewsList.slice()
        let sectionDivList = [];
        //add main story first
        let mainStory = getStoryAndUpdateList([artCat.isComplete, artCat.popular], tempNewsListCopy);
        if (mainStory.length === 0) {
            return "";
        }
        sectionDivList.push(mainStory)
        let stdColList = [];
        [stdColList, tempNewsListCopy] = getStdColsList(tempNewsListCopy, 2)
        if (stdColList.length === 0) return ""
        sectionDivList.push(stdColList[0]);
        sectionDivList.push(stdColList[1]);
        // sectionDivList.push(getStdColsList(tempNewsListCopy))
        console.debug("will update tempNewsList from copy", tempNewsList.length, tempNewsListCopy.length)
        tempNewsList = tempNewsListCopy.slice()
        console.debug("updated tempNewsList from copy", tempNewsList.length, tempNewsListCopy.length)

        return <section className="top">
            <div className="topLeft">
                <NewsCard newsStory={sectionDivList[0]} newsType={"main"}/>
            </div>
            <div className="topRight">
                <div className="stdCol"> {sectionDivList[1].map((st) => <NewsCard newsStory={st}
                                                                                  newsType={"normal"}/>)}</div>
            </div>
            <div className="topRight">
                <div className="stdCol"> {sectionDivList[2].map((st) => <NewsCard newsStory={st}
                                                                                  newsType={"normal"}/>)}</div>
            </div>
        </section>

    }

    function getSection2() {
        let tempNewsListCopy = tempNewsList.slice();
        let stdColsList = [];
        [stdColsList, tempNewsListCopy] = getStdColsList(tempNewsListCopy, 4);
        console.log("stdColList is :" + stdColsList);
        if (stdColsList.length === 0) return ""
        tempNewsList = tempNewsListCopy.slice();
        return <section className="middle">
            {stdColsList.map((stdCol) => <div className="stdCol"> {stdCol.map((st) => <NewsCard
                newsStory={st} newsType={"normal"}/>)}</div>)}
        </section>
    }

    function getSection3() {
        let tempNewsListCopy = tempNewsList.slice();
        let section3Stories = [];
        console.debug("section 3 stories list contains", section3Stories)
        section3Stories.push()
        section3Stories.push(getStoryAndUpdateList([artCat.hasImage], tempNewsListCopy))
        section3Stories.push(getStoryAndUpdateList([artCat.hasImage], tempNewsListCopy))
        if (section3Stories.some((item) => item === "")) return ""
        tempNewsList = tempNewsListCopy.slice();
        return <section className="bottom">
            <NewsCard newsStory={section3Stories[0]} newsType={"bottom"}/>
            <NewsCard newsStory={section3Stories[1]} newsType={"bottom"}/>
        </section>
    }

    function getSection4() {
        let newsList = tempNewsList.splice(0, 4)
        // newsList = newsList.map((st) => st.htImage = "");
        return <section className="middle last">
            {newsList.map((st) => <NewsCard
                newsStory={st} newsType={"normal"}/>)}
        </section>
    }

    function addCategoriesToStory(story) {
        let catList = [];
        if (isItPopular(story)) {
            catList.push(artCat.popular)
        } else {
            catList.push(artCat.notPopular)
        }
        if (story.htCurrentImage === undefined && story.htCurrentSubtitle === undefined) {
            catList.push(artCat.justTitle)
        } else if (story.htCurrentImage !== undefined && story.htCurrentSubtitle !== undefined) {
            catList.push(artCat.isComplete)
        }
        if (story.htCurrentImage === undefined) {
            catList.push(artCat.hasNoImage)
        } else {
            catList.push(artCat.hasImage)
        }

        if (story.htCurrentSubtitle === undefined) {
            catList.push(artCat.hasNoSubs)
        } else {
            catList.push(artCat.hasSubs)
        }
        story.categories = catList;
        return story
    }

    function isItPopular(article) {
        let a = new Date(article.time);
        let b = new Date().getTime();
        a = a * 1000;
        // console.log(`b ${b} a:${a}`);
        let days = (b - a) / (1000 * 60 * 60 * 24);
        // console.log(`days:${days}`);
        let day = Math.floor(days);
        let hrs = Math.floor(days * 24);
        let mints = Math.floor((days - day) * 60);

        let pointsSoFar = article.score;
        if ((hrs < 12 && hrs >= 0) &&
            pointsSoFar >= (hrs * 10)) {
            // console.log('popular 1');
            return true;
        }
        return hrs >= 12 && pointsSoFar >= 120;
    }


    function getDaysNameList(locale) {

        var d = new Date();
        d.setDate(d.getDate());
        let result = [];
        for (let i = 2; i < 7; i++) {
            d.setDate(new Date().getDate() - i);
            result.push(d.toLocaleDateString(locale, {weekday: 'long'}).toUpperCase());
        }
        // console.log(result);
        setTabsList(result);
    }

    function selectedTabClassName(tabId) {
        let url = props.newsUrl;
        if (url.includes(tabId)) return "selectedTab"
        else if (url.includes("top") && tabId === "latest") return "selectedTab"
        else return ""
    }

    useEffect(() => {
        console.log("useffect called");
        // setStoryNo(0);
        getDaysNameList();
        fetchNews()
    }, [props.newsUrl])

    return (
        <div className="homepage">
            <div className="websitetitle">
                <h1>The McLaren Times</h1>
            </div>
            <div className="navbar">
                <Link to={`/mc-times/latest`} style={{textDecoration: 'none'}}
                      className={"aLink " + selectedTabClassName("latest")}>
                    <div className="menuitem">LATEST</div>
                </Link>
                <Link to={`/mc-times/1`} style={{textDecoration: 'none'}}
                      className={"aLink " + selectedTabClassName("1day")}>
                    <div className="menuitem">YESTERDAY</div>
                </Link>
                {tabsList.map((day, idx) =>
                    (<Link to={`/mc-times/${idx + 2}`} style={{textDecoration: 'none'}}
                           className={"aLink " + selectedTabClassName(`${idx + 2}day`)}>
                        <div className="menuitem">{day}</div>
                    </Link>)
                )}

            </div>

            {allNews.length === 0 ? (
                <div className="newsbody" style={{height: "100vh"}}>
                    <div className={"loadingScreen"}>
                        <div className="lds-spinner">
                            <div></div>
                            <div></div>
                            <div></div>
                            <div></div>
                            <div></div>
                            <div></div>
                            <div></div>
                            <div></div>
                            <div></div>
                            <div></div>
                            <div></div>
                            <div></div>
                        </div>
                    </div>
                </div>
            ) : (
                <div className="newsbody">
                    {allNews}

                </div>
            )}
        </div>

    );

}
