import React from 'react'
import './styles.css'
import commentIcon from '../images/comments06.svg';
import menuVert from '../images/menuVert.svg';
import '../fonts/Roboto-Bold.ttf'
import '../fonts/Roboto-Medium.ttf'
import '../fonts/Roboto-Regular.ttf'
import '../fonts/NotoSerif-Bold.ttf'
import '../fonts/NotoSerif-Regular.ttf'
import '../fonts/RobotoSlab-Medium.ttf'
import '../fonts/RobotoSlab-Regular.ttf'
import '../fonts/RobotoSlab-Bold.ttf'


export default function NewsCard(props) {
    const newsSection = props.newsType;

    const article = props.newsStory;

    const isItPopular = (article) => {
        let a = new Date(article.time);
        let b = new Date().getTime();
        a = a * 1000;
        // console.log(`b ${b} a:${a}`);
        let days = (b - a) / (1000 * 60 * 60 * 24);
        // console.log(`days:${days}`);
        let day = Math.floor(days);
        let hrs = Math.floor(days * 24);
        let mints = Math.floor((days - day) * 60);

        let pointsSoFar = article.score;
        if ((hrs < 12 && hrs >= 0) &&
            pointsSoFar >= (hrs * 10)) {
            // console.log('popular 1');
            return true;
        }
        return hrs >= 12 && pointsSoFar >= 120;
    };
    const isNewsStoryPopular = isItPopular(props.newsStory);

    function getNewsCardClass() {
        if (newsSection === "main" && isNewsStoryPopular)
            return "newsCard main-card main-popular-card"
        else if (newsSection === "bottom" && isNewsStoryPopular)
            return "newsCard bottom-card bottom-popular-card"
        else if (newsSection === "normal" && isNewsStoryPopular)
            return "newsCard normal-card normal-popular-card"
        else if (newsSection === "normal")
            return "newsCard normal-card"
        else if (newsSection === "bottom")
            return "newsCard bottom-card"
        else if (newsSection === "main" && isNewsStoryPopular)
            return "newsCard main-card" //not needed since main news will always be popular
    }

    function extractDomainFromUrl() {
        const url = document.createElement('a');
        if (!article['url']) return ""
        let urlStr = article['url'];
        try {
            url.setAttribute('href', urlStr);
            let host = url.hostname.replace("www.", "")

            return host;
        } catch (e) {
            return "";
        }
    }

    function dateDiff(a, b) {
        a = a * 1000;
        // console.log(`b ${b} a:${a}`);
        let days = (b - a) / (1000 * 60 * 60 * 24);
        // console.log(`days:${days}`);
        let day = Math.floor(days);
        if (day > 0) return day + "d ago"; //if article is more than a day old then display  x d ago
        // days *= 24;
        let hrs = Math.floor(days * 24);
        let mints = Math.floor((days - day) * 60);
        return hrs + "h" + mints + "m" + " ago";
    }

    function getImageClass() {
        if (newsSection === "main")
            return "main-img"
        if (newsSection === "bottom")
            return "bottom-img"
        if (newsSection === "normal")
            return "nm-img"
    }

    function getImageDiv() {
        // console.log("article url is ")
        // console.log(article.url)
        let noSubs = article.htCurrentSubtitle === undefined ? " imgWithNoSubtitleContainer" : "";
        if (article.htCurrentImage !== undefined)
            return (
                <div
                    className={isNewsStoryPopular ? "imgContainer popNewsImgContainer" + noSubs : "imgContainer" + noSubs}>
                    <img style={{maxHeight: "100%", maxWidth: "100%",}} onClick={() => window.open(article.url,)}
                         className={getImageClass() + imageWithNoSubtitle()}
                         src={article.htCurrentImage == null ? "" : article.htCurrentImage}
                         alt=""/>
                </div>
            )
        return "";
    }

    function imageWithNoSubtitle() {
        if (article.htCurrentSubtitle == null)
            return " .imgWithNoSubtitle"
        return "";
    }

    function modifySubtitle(subtitle) {
        let trimmedSubtitle = subtitle.split(" ").slice(0, 35).join(" ");
        if (newsSection === "main") {
            return subtitle.split(" ").slice(0, 75).join(" ");
        }

        if (trimmedSubtitle.length === subtitle.length) return subtitle;

        return trimmedSubtitle + " ...."
    }

    function getSubtitleDiv() {
        return article.htCurrentSubtitle == null ? "" :
            <div className="subtitle" onClick={() => window.open(article.url,)}>
                {modifySubtitle(article.htCurrentSubtitle)}
            </div>
    }

    function getAllFooter() {
        return (
            <div className="footer">
                <div className="footerA">
                    <div className="src" onClick={() => window.open(article.url,)}>
                        {extractDomainFromUrl()}
                    </div>
                    <div className="hrs-ago">
                        {dateDiff(article.time, Date.parse(new Date()))}
                    </div>
                </div>
                <div className="footerB">
                    <div className="comments-row">
                        <div className="comment-img-container">
                            <img className="cmntSvgIcon" src={commentIcon} alt=""/>
                        </div>
                        <div className="comment-txt"
                             onClick={() => window.open("https://news.ycombinator.com/item?id=" + article.id,)}>
                            {article.descendants} comments
                        </div>
                    </div>
                    <div className="points">
                        {article.score} points
                    </div>
                    <div className="vertMenuDiv">
                        <img src={menuVert} alt=""/>
                    </div>
                </div>
            </div>
        );
    }

    function getBodyDivs() {
        return (<div className="sub-body">
            {getImageDiv()}
            <div className="txt-bodyContent">
                {isItPopular(article) && newsSection !== "bottom" ? getHeadlineDiv() : ""}
                {newsSection === "bottom" ? getHeadlineDiv() : ""}
                {getSubtitleDiv()}
                {newsSection === "bottom" ? getAllFooter() : ""}
            </div>
        </div>)
    }

    function getHeadlineDiv() {
        return (<div className="hl" onClick={() => window.open(article.url,)}>
            {article.title}
        </div>)
    }

    return (
        <div className={getNewsCardClass()}>
            {newsSection !== "bottom" && !isItPopular(article) ? getHeadlineDiv() : ""}
            <div className="body">

                {getBodyDivs()}
            </div>
            {newsSection !== "bottom" ? getAllFooter() : ""}
        </div>
    );


}
